:root{
    font-size: 16px;
    font-family: 'Open Sans';
    --text-primary: #b6b6b6;
    --text-secondary: #ececec;
    --bg-primary: #23232e;
    --bg-secondary: #141418;
}

.cus-navbar{
    width: 5rem;
    height: 100vh;
    position:fixed;
    background-color: var(--bg-primary);
}

.cus-navbar-nav{
    list-style: none;
    padding: 0;
    margin:0;
    display:flex;
    flex-direction: column;
    align-items: center;
    height:100%
}
.cus-nav-item{
    width:100%;
}
.cus-nav-item:last-child{
    margin-top:auto;
}

.cus-nav-link{
    display: flex;
    align-items: center;
    height:5rem;
    color:var(--text-primary);
    text-decoration: none;
    filter: grayscale(100%) opacity(0.7);
    transition: var(--transition-speed);
}

.cus-nav-link:hover{
    text-decoration: none;
    filter: grayscale(0%) opacity(1);
    background: var(--bg-secondary);
    color: var(--text-secondary)
}
.cus-navbar:hover{
    width:10rem;
}
.cus-navbar:hover .link-text{
    display: block;
}

.link-text{
    display: none;
    margin-left:0.5 rem;
    color: var(--siteOrange);
    transition: var(--transition-speed);


}
.cus-nav-link svg{
    min-width: 2rem;
    margin: 0 1.5rem;
    color: var(--siteOrange);
    transition: var(--transition-speed)

}

/*small screen */
@media only screen and (max-width:600px){
    .cus-navbar{
        bottom:0;
        width:100vw !important;
        height:5rem;
    }
    
    .cus-navbar:hover .link-text{
        display: inline;
        transition: opacity var(--transition-speed)
    }
    .cus-navbar-nav{
        flex-direction: row;
    }
    .cus-nav-link{
        justify-content: center;
    }
    main{
        margin:0
    }
}
